<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot>
      </slot>
    </div>
    <div class="dots">
      <span class="dot" :key="index" v-for="(item,index) in dots" :class="{active: currentPageIndex === index}"></span>
    </div>
  </div>
</template>

<script>

import {addClass} from 'common/js/dom'
import BScroll from 'better-scroll'
export default {
  data() {
    return {
      dots: [],
      currentPageIndex: 0
    }
  },
  props: {
    loop: {
      type: Boolean,
      default: true
    },
    autoPlay: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 4000
    }
  },
  mounted () {
    setTimeout(() => {
      this._setSliderWidth()
      this._initDots()
      this._initSlider()
      if (this.autoPlay) {
        this._play()
      }
    }, 20)
    // 
    window.addEventListener('resize', () => {
      // slider 
      if (!this.slider) {
        return
      }
      // 
      this._setSliderWidth(true)
      this.slider.refresh()
    })
  },
  destroyed() {
    // slider  
    clearTimeout(this.timer)
  },
  methods: {
    _setSliderWidth (isResize) {
      // 
      this.children = this.$refs.sliderGroup.children
      // banner
      let sliderWidth = this.$refs.slider.clientWidth
      let width = 0
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        // 
        addClass(child, 'slider-item')
        // 
        child.style.width = sliderWidth + 'px'
        width += sliderWidth
      }
      if (this.loop && !isResize) {
        //    
        width += 2 * sliderWidth
      }
      // 
      this.$refs.sliderGroup.style.width = width + 'px'
    },
    _initSlider() {
      this.slider = new BScroll(this.$refs.slider, {
        scrollX: true, // 
        scrollY: false, // 
        momentum: false, // 
        snap: true, //   slider 
        snapLoop: this.loop, // 
        snapThreshold: 0.3, // 
        snapSpeed: 400 // 
        // click: true a, click click  fastclick  BScrollclick  better-scroll  fastclick 
      })
      this.slider.on('scrollEnd', () => {
        // index
        let pageIndex = this.slider.getCurrentPage().pageX
        //  
        if (this.loop) {
          pageIndex -= 1
        }
        this.currentPageIndex = pageIndex
        //   
        if (this.autoPlay) {
          clearTimeout(this.timer)
          this._play()
        }
      })
    },
    _play() {
      // 
      let pageIndex = this.currentPageIndex + 1
      //  
      if (this.loop) {
        pageIndex += 1
      }
      this.timer = setTimeout(() => {
        // pageIndex  false 
        this.slider.goToPage(pageIndex, 0, 400)
      }, this.interval)
    },
    _initDots() {
      // banner 
      this.dots = new Array(this.children.length)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  .slider
    min-height: 1px
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
</style>



