import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions' // actions...
import * as getters from './getters' // getter.singer ... {}
import state from './state'
import mutations from './mutations' // import
import createLogger from 'vuex/dist/logger' // mutationsstate console.log   
Vue.use(Vuex)

// vuex mutationsstate ,  debug === true
const debug = process.env.NODE_ENV !== 'production' // npm run build -> production  npm run dev -> dev

export default new Vuex.Store({
  actions,
  getters,
  state,
  mutations,
  //  
  strict: debug,
  plugins: debug ? [createLogger()] : []
})



